<template>
  <div class='doctorSpace-box'>
    <div class="doctor-title mb-10">
      <van-image 
      round
      fit="cover"
      width="5rem"
      height="5rem"
      class="mr-10 "
      :show-loading='true'
      src="../../../assets/img/yisheng.png">
        <template v-slot:error>
          <van-image
            fit="cover"
            round
            width="5rem"
            height="5rem"
            src="../../../assets/img/yisheng.png"
          />
        </template>
      </van-image>
      <div class="doctorData-box">
        <p class="mb-10 mt-5">周大夫</p>
        <p class="mb-5 fx12"><span class="mr-10">主任医生</span><span>康复医科</span></p>
        <p class="fx12">术后康复</p>
      </div>
    </div>
    <div class="tips mb-10">
      <span class="ml-20 sp1">这里显示所在科室</span>
      <span class="mr-20 sp2">排班信息</span>
    </div>
    <div class="dpctorScheduling-box ">
      <div class="first-title">
        <div>下午</div>
        <div>上午</div>
        <div>排班</div>
      </div>
      <div class="schedulingUl-box">
        <table cellpadding='0' cellspacing='0'>
          <tr >
            <td v-for="(item,index) in schedulingList" :key="index">
              <div class="date-box">
                <span class="sp1 fx14">{{item.date}}</span>
                <span class="fx12">{{item.week}}</span>
              </div>
            </td>
          </tr>
          <tr>
            <td v-for="(item,index) in schedulingList" :key="index" >
              <div :class="{'active':item.am.active != false}" @click="activeDoctorFn(index,'am')">
                <img src="../../../assets/img/gougou.png" v-if="item.am.active != false">
                <span class="sp2">剩余号<br/>源：{{item.am.data}}</span>
              </div>
            </td>
          </tr>
          <tr>
            <td v-for="(item,index) in schedulingList" :key="index" > 
              <div  :class="{'active':item.pm.active != false}" @click="activeDoctorFn(index,'pm')">
                <img src="../../../assets/img/gougou.png" v-if="item.pm.active != false">
                <span class="sp2">剩余号<br/>源：{{item.pm.data}}</span>
              </div>
            </td>
          </tr>
        </table>
      </div>
    </div>
    <div class="confirmOrder-box mt-20 ml-20 pb-20">
      <span>挂号费：￥<span class="money">{{money}}</span></span>
      <van-button color="#FFA800" @click="goConfirmOrderFn" class="mr-20" size="small" round type="info">立即预约</van-button>
    </div>
  </div>
</template>

<script>
  export default {
    name: '',
    data(){
      return{
        schedulingList:[],
        money:0,
      }
    },
    mounted(){
      this.getDateListFn()
    },
    methods:{
      activeDoctorFn(index,n){
        this.schedulingList.forEach(item=>{
          item.am.active = false
          item.pm.active = false
        })
        this.schedulingList[index][n].active = true;
      },
      getDateListFn(){
        let dateList = [];
        for(let i = 0; i<14 ; i++){
          let date = this.getDateStr(i).split(',')[1]
          let week = this.getDateStr(i).split(',')[0]
          let data = {
            date:date,
            week:week,
            am:{data:0,active:false},
            pm:{data:20,active:false},
            }
          dateList.push(data)
        }
        this.schedulingList = dateList
      },
      getDateStr(AddDayCount) { 
        var dd = new Date();
        dd.setDate(dd.getDate()+AddDayCount);//获取AddDayCount天后的日期
        var y = dd.getFullYear(); 
        var m = (dd.getMonth()+1)<10?"0"+(dd.getMonth()+1):(dd.getMonth()+1);//获取当前月份的日期，不足10补0
        var d = dd.getDate()<10?"0"+dd.getDate():dd.getDate();//获取当前几号，不足10补0
        var week = dd.getDay()
          switch(week){
            case 1:
                week='星期一';
                break;
            case 2:
                week='星期二';
                break;
            case 3:
                week='星期三';
                break;
            case 4:
                week='星期四';
                break;
            case 5:
                week='星期五';
                break;
            case 6:
                week='星期六';
                break;
            case 0:
                week='星期日';
                break;
        }
        return week+','+m+"-"+d; 
      },
      goConfirmOrderFn(){
          this.$router.push({path:'/confirmOrder',query:{setid:123456}})
      }
    }
  };
</script>
<style lang='scss'>
.doctorSpace-box{
  background: #fff;
  .tips{
    display: flex;
    justify-content: space-between;
    .sp1{color: #333;}
    .sp2{color: #999;font-size: 14px;}
  }
  .doctor-title{
    display: flex;
    justify-content: start;
    background: url(../../../assets/img/doctorBg.png);
    background-size: 100% 100%;
    padding: 10px;
    .doctorData-box{
      p{color: #fff;}
    }
  }
  
  .confirmOrder-box{
    font-size: 16px;
    display: flex;
    justify-content: space-between;
    span.money{
      color: #FFA800
    }
  }
  .dpctorScheduling-box{
    font-size: 12px;
    display: flex;
    .first-title{
      writing-mode:vertical-lr;
      display: flex;
      flex-direction: row-reverse;
      border-top: 1px solid #ddd;
      width: 20px;
      div{
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-around;
        flex-direction: column;
        border-bottom: 1px solid #ddd;
        font-size: 14px;
      }
    }
    .schedulingUl-box{
      overflow: scroll;
      table{
        border-bottom: 1px solid #ddd;
        tr{
          td+td{
            border-left: none;
          }
          td{
            border: 1px solid #ddd;
            text-align: center;
            border-bottom: none;
            .date-box{
              height: 100%;
              width: 60px;
              text-align: center;
              padding: 10px 0;
              span{display: block;}
            }
            div{
              width: 60px;
              position: relative;
              img{
                position: absolute;
                top: 0;
                right: 0;
              }
              span{
                display: block;
                width: 60px;
                text-align: center;
                font-size: 14px;
              }
              .sp2{
                display: block;
                width: 100%;
                height: 100%;
                background: #F4F8FC;
                color: #999999;
                padding: 10px 0;

              }
            }
            div.active{
              .sp2{
                color: #3a83e7;
              }
            }
          }
          
        }
      }
      
    }
    
  }
}
</style>


